
import { Options, Vue } from "vue-class-component";

@Options({})
export default class Header extends Vue {
  currentDate = "DD-MMM-YYYY";

  mounted() {
    this.manageDate();
  }

  manageDate(): void {
    const date = new Date();
    const formattedDate = date
      .toLocaleDateString("en-GB", {
        day: "numeric",
        month: "short",
        year: "numeric",
      })
      .replace(/ /g, "-");
    this.currentDate = formattedDate;
  }
}
